<script>
  import { lightTheme } from "../util/stores.js";

  function toggleTheme() {
    if ($lightTheme === "false") {
      $lightTheme = "true";
    } else {
      $lightTheme = "false";
    }
    window.document.body.classList.toggle("light-theme");
  }
</script>

<div id="themeChooserBox">
  <img
    id="themeImg"
    alt="Click to toggle dark and light theme"
    src="../images/white_bulb_off.png"
    on:click={toggleTheme}
  />
</div>

<style type="text/scss">#themeChooserBox {
  width: 55px;
  height: 100%;
  display: inline-block;
  box-sizing: border-box;
  padding: 5px;
  margin: 0;
  flex-grow: 0;
  flex-grow: 0; }
  #themeChooserBox:hover {
    background-color: rgba(0, 0, 0, 0.8); }

:global(body.light-theme) #themeChooserBox:hover {
  background-color: rgba(250, 250, 250, 0.5); }

img {
  border: none;
  width: 100%; }
  img:hover {
    content: url("../images/white_bulb_on.png"); }

:global(body.light-theme) #themeImg {
  content: url("../images/black_bulb_on.png"); }
  :global(body.light-theme) #themeImg:hover {
    content: url("../images/black_bulb_off.png"); }

/*# sourceMappingURL=ThemeChooser.svelte.css.map */</style>
