<script>
    import { localPreset, linkOverlayOptions } from '../util/stores.js';
    import { Hyperlink } from '../util/hyperlink.js';
    import { onMount } from 'svelte';

    export let newLinkName = undefined;
    export let newLinkUrl = undefined;
    
    $:submittable = newLinkName && newLinkUrl;
    let type = $linkOverlayOptions.currLinkName ? "edit" : "new";
    let nameInput;

	onMount(() => {
		nameInput.focus();
	});

    function closeOverlay() {
        $linkOverlayOptions.showOverlay = false;
        $linkOverlayOptions.currentFolderId = undefined;
        $linkOverlayOptions.currentFolder = undefined;
        $linkOverlayOptions.currLinkId = undefined;
        $linkOverlayOptions.currLinkName = undefined;
        $linkOverlayOptions.currLinkUrl = undefined;
    }

    function addLink() {
        if(submittable) {
            let currPreset = $localPreset;
            let currLinks = currPreset.Folders[$linkOverlayOptions.currentFolderId].links;
    
            currLinks.push(new Hyperlink(
                currLinks.length,
                newLinkName,
                newLinkUrl)
            );        
    
            $localPreset = currPreset;
            closeOverlay();
        }
    }

    function editLink() {
        if(submittable){
            let currPreset = $localPreset;
            let currLink = currPreset.Folders[$linkOverlayOptions.currentFolderId]
                .links[$linkOverlayOptions.currLinkId];
    
            currLink.linkName = newLinkName;
            currLink.linkUrl = newLinkUrl;
    
            $localPreset = currPreset;
            closeOverlay();
        }
    }
</script>


<section>
    <button id="exitOverlay" on:click={closeOverlay}>
        X
    </button>
    <div id="insertHead">
        {#if type === "new"}
            <h1>Create a new link</h1>
        {:else}
            <h1>Edit ''{newLinkName}'' link</h1>
        {/if}
        <h2>Within ''{$linkOverlayOptions.currentFolder}''</h2>
    </div>
    <div id="insertContent"> 
        <input bind:this={nameInput}
            maxlength="40"
            type="text" 
            bind:value={newLinkName} 
            placeholder="Insert link name"
            on:keyup={ event => {
                if(event.keyCode === 13) {
                    type === "new" ? addLink() : editLink()
                }
            }}>

        <input
            type="text" 
            bind:value={newLinkUrl} 
            placeholder="Insert link name"
            on:keyup={ event => {
                if(event.keyCode === 13) {
                    type === "new" ? addLink : editLink
                }
            }}>
    </div>
    <button id="submitOverlay"
        class={!submittable ? "disabled" : "enabled"}
        on:click={type === "new" ? addLink : editLink}
        disabled={!submittable}>
        Save
    </button>
</section>
<svelte:window on:keyup={
    event => event.keyCode === 27 ? closeOverlay() : "foo"}/>



